@import "../../../styles/variables.scss";

.nav {
  height: 100%;
  display: none;
  align-items: center;

  ul {
    height: 100%;
    display: flex;
    margin-right: $spacing-lg;
    li {
      height: 100%;
      &:not(:last-child) {
        margin-right: $spacing-md;
      }
      a {
        display: flex;
        align-items: center;
        height: 100%;
        font-size: 1.1rem;
        letter-spacing: $letterSpacing;
        text-decoration: none;
        color: $white;
        position: relative;
        &::after {
          content: "";
          position: absolute;
          width: 100%;
          height: 3px;
          bottom: 0;
          left: 0;
          right: 0;
          background: $white;
          opacity: 0;
          transition: 0.3s ease opacity;
        }

        &:hover::after {
          opacity: 1;
        }
        &:hover {
          color: $white;
        }
      }
    }
  }

  @include breakpoint(md) {
    display: flex;
  }
}

.menu__nav {
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;

  ul {
    display: flex;
    flex-direction: column;
    margin-bottom: $spacing-lg;

    li {
      &:not(:last-child) {
        margin-bottom: $spacing-md;
      }

      a {
        text-decoration: none;
        color: $white;

        &:hover {
          color: $light2;
        }
      }
    }
  }
}
